@import url("https://fonts.googleapis.com/css2?family=Barlow+Condensed:wght@300&display=swap");
* {
  padding: 0;
  margin: 0;
  box-sizing: border-box;
}

html {
  font-size: 20px;
  font-family: Arial, Helvetica, sans-serif;
}

p,
h1,
h2,
h3,
h4,
span,
div,
input {
  font-weight: normal;
}

input {
  outline: none;
  border: 1px solid rgba(85, 85, 85, 0.768);
}

.text-shadow {
  min-height: 100vh;
  width: 100%;
  display: flex;
  justify-content: center;
  align-items: center;
  background-color: #58357e;
}
.text-shadow__container {
  width: 96%;
  max-width: 600px;
  height: 100%;
  max-height: 700px;
  display: flex;
  flex-wrap: wrap;
  justify-content: space-around;
  align-items: flex-start;
  padding: 2% 0;
  border: 1px solid rgba(85, 85, 85, 0.768);
  background-color: #a87d45;
}

.text-shadow__element {
  flex-basis: 95%;
  display: flex;
  flex-wrap: wrap;
  align-items: center;
  justify-content: flex-end;
  border: 1px solid rgba(85, 85, 85, 0.768);
  margin: 2px 0;
  padding: 2px 0;
  background-color: #d9c1a2;
}
@media (min-height: 500px) {
  .text-shadow__element {
    margin: 5px 0;
    padding: 5px 0;
  }
}

.text-shadow__text-area {
  display: flex;
  justify-content: center;
  height: 18vh;
  background-color: white;
  overflow: hidden;
}
@media (orientation: landscape) {
  .text-shadow__text-area {
    height: 24vh;
  }
}
@media (min-height: 500px) {
  .text-shadow__text-area {
    height: 20vh;
    font-size: 2.2rem;
  }
}

.text-shadow__text-area-p {
  font-size: 2.4rem;
  font-weight: bold;
}

.text-shadow__element-p {
  margin: 0 3px;
  color: #4f2977;
}

.text-shaddow__element-area {
  position: relative;
  flex-basis: 70%;
  margin: 0 10px;
  border: 1px solid rgba(85, 85, 85, 0.768);
  overflow: hidden;
  box-shadow: inset 1px 1px 2px rgba(0, 0, 0, 0.2);
  background-color: #f2eae0;
  height: 40px;
}

.text-shadow__element-area-zip {
  position: absolute;
  display: flex;
  justify-content: center;
  align-items: center;
  top: 0;
  bottom: 0;
  left: 0;
  width: 40px;
  height: 100%;
  background-color: brown;
  z-index: 1;
  cursor: pointer;
  box-shadow: inset 1px 1px 2px rgba(255, 255, 255, 0.4);
}

.fas {
  color: rgba(255, 54, 54, 0.529);
  padding: 4px;
  font-size: 1.2rem;
  text-shadow: 1.5px 1.5px 1.5px rgba(0, 0, 0, 0.405);
}

.box-shadow__copy {
  padding: 10px 0;
  justify-content: center;
}

.box-shadow__copy-p {
  flex-basis: 100%;
  line-height: 1.4;
  text-align: center;
  cursor: pointer;
  transition: transform 0.1s;
  color: #4f2977;
}
@media (min-height: 500px) {
  .box-shadow__copy-p {
    line-height: 2;
  }
}
.box-shadow__copy-p:hover {
  transform: scale(1.1);
}

.box-shadow__copy-input {
  height: 30px;
  width: 95%;
  font-size: 0.7rem;
  font-family: "Barlow Condensed", sans-serif;
  letter-spacing: 0;
  text-align: center;
}
@media (min-height: 500px) {
  .box-shadow__copy-input {
    height: 40px;
  }
}
@media (min-width: 359px) {
  .box-shadow__copy-input {
    font-size: 0.8rem;
  }
}
@media (min-width: 430px) {
  .box-shadow__copy-input {
    font-size: 1rem;
  }
}
@media (min-width: 500px) {
  .box-shadow__copy-input {
    font-size: 1.2rem;
  }
}